import template from './sw-cms-el-kohlkramer-theme-product-box.html.twig';
import './sw-cms-el-kohlkramer-theme-product-box.scss';

const { Component, Mixin, Filter } = Shopware;

Component.register('sw-cms-el-kohlkramer-theme-product-box', {
    template,

    inject: ['feature'],

    mixins: [
        Mixin.getByName('cms-element'),
        Mixin.getByName('placeholder'),
    ],

    computed: {
        product() {
            if (!this.element.data || !this.element.data.product) {
                return {
                    name: 'Lorem ipsum dolor',
                    description: `Lorem ipsum dolor sit amet, consetetur sadipscing elitr,
                    sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat,
                    sed diam voluptua.`.trim(),
                    price: [
                        { gross: 19.90 },
                    ],
                    cover: {
                        media: {
                            url: '/administration/static/img/cms/preview_glasses_large.jpg',
                            alt: 'Lorem Ipsum dolor',
                        },
                    },
                };
            }

            return this.element.data.product;
        },

        displaySkeleton() {
            return !this.element.data || !this.element.data.product;
        },

        mediaUrl() {
            if (this.product.cover && this.product.cover.media) {
                if (this.product.cover.media.id) {
                    return this.product.cover.media.url;
                }

                return this.assetFilter(this.product.cover.media.url);
            }

            return this.assetFilter('administration/static/img/cms/preview_glasses_large.jpg');
        },

        altTag() {
            if (this.product.cover && this.product.cover.media && this.product.cover.media.alt) {
                return this.product.cover.media.alt;
            }

            return null;
        },

        displayModeClass() {
            if (this.element.config.displayMode.value === 'standard') {
                return null;
            }

            return `is--${this.element.config.displayMode.value}`;
        },

        verticalAlignStyle() {
            if (!this.element.config.verticalAlign || !this.element.config.verticalAlign.value) {
                return null;
            }

            return `align-content: ${this.element.config.verticalAlign.value};`;
        },

        assetFilter() {
            return Filter.getByName('asset');
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('kohlkramer-theme-product-box');
            this.initElementData('kohlkramer-theme-product-box');
        },
    },
});
